<template>
  <div>
    <div>
      <el-card shadow='hover' class='card'>
        <el-divider class='divider' content-position="right" @click='closeMarketDetail'>
          <div class='back'>
            <el-image class='backIcon' :src="require('@/../public/assets/return.png')"></el-image>
            <span class='backWord'>{{$t('Market.DetailReturn')}}</span>
          </div>
        </el-divider>

        <div class='content'>
          <div class='itemInfo section'>
            <div class='itemName'>
              <span class='name'>{{itemName}}</span>
              <span class='label'>{{$t('Market.DetailName')}}</span>
            </div>
            <div class='itemVersion item'>
              <span class='left'>{{$t('Market.DetailVersion')}}</span>
              <span class='right'>{{itemVersion}}</span>
            </div>
            <div class='itemPublish item'>
              <span class='left'>{{$t('Market.DetailPublish')}}</span>
              <span class='right'>{{itemPublish}}</span>
            </div>
          </div>

          <div class='itemClass item section'>
            <span class='left'>{{$t('Market.DetailClass')}}</span>
            <span class='right'>{{classname}} ({{school}})</span>
          </div>

          <div class='itemStatus section'>
            <div class='broken item'>
              <span class='left'>{{$t('Market.DetailBroken')}}</span>
              <span class='right'>{{broken}}</span>
            </div>
            <div class='written item'>
              <span class='left'>{{$t('Market.DetailWritten')}}</span>
              <span class='right'>{{written}}</span>
            </div>
          </div>

          <div class='picture section'>
            <el-image :src="picture" width="200px"></el-image>
          </div>

          <div class='user section'>
            <div class='username item'>
              <span class='left'>{{$t('Market.DetailUser')}}</span>
              <span class='right' @click='openUserPage(userID)'>{{username}} ({{realname}})</span>
            </div>
            <div class='username item'>
              <span class='left'>{{$t('Market.DetailTime')}}</span>
              <span class='right'>{{time}}</span>
            </div>
          </div>
        </div>

        <div class='buttons'>
          <el-image class='aAction' :src="require('@/../public/assets/share.png')" @click='clickShare'>
          </el-image>
          <el-image class='aAction' :src="require('@/../public/assets/delete.png')" v-if='isOwner||isAdmin'
            @click='clickDelete'>
          </el-image>
          <el-image class='aAction' :src="require('@/../public/assets/report.png')" v-else
            @click='clickReport("md",itemID)'>
          </el-image>
        </div>

        <div class='purchase'>
          <el-button type='primary' :disabled='isOwner' @click='purchase'>{{$t('Market.DetailPurchase')}} (ï¿¥{{price}}
            )</el-button>
        </div>
      </el-card>
    </div>

  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'market-detail',
  data () {
    return {
      itemID: 0,
      userID: 0,
      username: '',
      realname: '',
      itemName: '',
      itemVersion: '',
      itemPublish: '',
      classname: '',
      school: 0,
      price: 0,
      broken: 0,
      written: 0,
      picture: '',
      time: '',
      isOwner: false,
      isAdmin: false,
    }
  },
  methods: {
    clickShare () {
      this.copyLink(this.$t('Market.DetailShared'));
    },
    closeMarketDetail () {
      this.$bus.$emit('showMarketList')
    },
    getMarketDetail () {
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.getMarketItem,
        data: new URLSearchParams({
          'itemid': this.itemID,
          'cookie': Cookies.get('loginCookie')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'MARKET_GETITEM_COMPLETE') {
            if (this.itemID == response.data.itemID) {
              this.userID = response.data.userID;
              this.username = response.data.username;
              this.realname = response.data.realname;
              this.itemName = response.data.itemName;
              this.itemVersion = response.data.itemVersion;
              this.itemPublish = response.data.itemPublish;
              this.school = this.formatSchool(response.data.school);
              this.classname = response.data.classname;
              this.price = response.data.price;
              this.broken = response.data.broken + '%';
              this.written = response.data.written + '%';
              this.time = this.formatTime(response.data.time);
              this.picture = server.default.server + response.data.picture;
              this.isOwner = response.data.isOwner;
            }
          }
          else {
            this.notfound();
          }
        }
      })
    },
    clickDelete () {
      if (this.isOwner || this.isAdmin) {
        this.$confirm(this.$t('Market.DetailDeleteWarn'), this.$t('System.Warning'), {
          confirmButtonText: this.$t('System.Yes'),
          cancelButtonText: this.$t('System.No'),
          type: 'warning'
        }).then(() => {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.deleteMarketItem,
            data: new URLSearchParams({
              'cookie': Cookies.get('loginCookie'),
              'itemid': this.itemID,
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else if (response.data == 'MARKET_DELETE_COMPLETE') {
              this.$message.success(this.$t('Market.DetailDeleteComplete'));
              this.$bus.$emit('refreshMarketList');
              this.$bus.$emit('showMarketList');
            }
          })
        }).catch(() => { });
      }
    },
    clickReport (target, targetid) {
      this.$prompt(this.$t('General.ReportReason'), this.$t('General.Report'), {}).then((value) => {
        if (!value.value) {
          return;
        }
        else {
          this.report(Cookies.get('loginCookie'), target, targetid, value.value, location.href);
        }
      })
    },
    purchase () {
      this.$prompt(this.$t('Market.DetailPurchasePlace'), this.$t('Market.DetailPurchase'), {
        confirmButtonText: this.$t('System.Confirm'),
        cancelButtonText: this.$t('System.Cancel'),
      }).then(({ value }) => {
        if (value && value.length > 2) {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.purchaseMarketItem,
            data: new URLSearchParams({
              'alertid': nanoid(),
              'cookie': Cookies.get('loginAs'),
              'itemid': this.itemID,
              'userid': this.userID,
              'location': value
            })
          }).then((response) => {
            console.log(response);
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'MARKET_PURCHASE_ALERTIDTAKEN') {
                this.claimORontact(value);
              }
              else if (response.data == 'MARKET_PURCHASE_LATE') {
                this.alert(this.$t('Market.DetailPurchaseLate'), this.$t('System.Info'));
                this.closeMarketDetail();
              }
              else if (response.data == 'MARKET_PURCHASE_PURSELEAK') {
                this.alert(this.$('Market.DetailPurchasePurseLeak'), this.$t('System.Info'));
              }
              else if (response.data == 'MARKET_PURCHASE_COMPLETE') {
                this.$bus.$emit('refreshMarketList');
                this.alert(this.$t('Market.DetailPurchaseComplete'), this.$t('System.Info'));
                this.closeMarketDetail();
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'));
              }
            }
          });
        }
        else {
          this.purchase();
          return;
        }
      }).catch(() => { });
    }
  },
  mounted () {
    this.itemID = this.$route.params.id;
    this.getMarketDetail();
    this.isAdmin = this.getIfAdmin();
  }
}
</script>

<style scoped>
.card {
  margin-left: 5%;
  width: 90%;
  border-radius: 20px;
}
.divider {
  width: 90%;
  margin-left: 5%;
}
.back {
  cursor: pointer;
}
.backIcon {
  color: rgb(252, 62, 5);
  width: 20px;
}
.backWord {
  font-size: 18px;
  margin-left: 10px;
}
.content {
  text-align: left;
  margin-left: 13%;
  margin-top: 50px;
  margin-bottom: 40px;
  width: 74%;
  font-size: 25px;
}
.item {
  margin-top: 10px;
}
.section {
  margin-top: 30px;
}
.itemName .name {
  font-size: 40px;
  font-weight: bolder;
  margin-right: 20px;
}
.left,
.itemName .label {
  font-size: 20px;
}
.right {
  float: right;
  font-weight: bold;
}
.picture {
  text-align: center;
}
.picture .el-image {
  width: 200px;
  height: 280px;
}
.user .right {
  font-size: 20px;
  font-weight: normal;
  cursor: pointer;
}
.buttons {
  text-align: right;
  margin-right: 12%;
  font-size: 20px;
  margin-bottom: 20px;
}
.aAction {
  width: 25px;
  margin-right: 15px;
  cursor: pointer;
}
.purchase .el-button {
  font-size: 20px;
  width: 20%;
  border-radius: 15px;
}
</style>